.App {
  text-align: center;
}

.teammates-tools {
  padding: 15px;
  padding-bottom: 0;
}

.teammates-tools__item {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
  padding: 5px;
  width: min-content;
  max-width: 100%;
  overflow: hidden;
}

.teammates-tools__item svg {
  width: 14px;
  height: 14px;
  fill: #6f6e7c;
}

.teammates-tools__item img {
  height: 16px;
}

.teammates-tools__item span {
  color: #52515b;
  font-size: 12px;
  font-weight: 400;
  margin-left: 10px;
  white-space: nowrap;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.teammates-tools__item a {
  color: #52515b;
  font-size: 8px;
  font-weight: 400;
  text-decoration: none;
}
.teammates-tools__item:hover {
  border-radius: 5px;
  background-color: #c1eeff;
}

.teammates-tools__item:hover a {
  color: #00b9ff;
  text-decoration: none;
}

.teammates-tools__item:hover svg {
  fill: #00b9ff;
}
.teammates-tools__item:hover span {
  color: #00b9ff;
}

.teammates-tools__write {
  position: fixed;
  bottom: 0;
  padding: 0;
  margin-bottom: 8px;
  width: 96%;
  margin-left: 1.5%;
  box-sizing: border-box;
}
.teammates-tools__write input {
  width: 100%;
  border-radius: 24px;
  border: solid 1px #eeeeee !important;
  color: #52515b;
  font-size: 12px;
  font-weight: 400;
  border: none;
  padding: 16px 10px;
  display: block;
  box-sizing: border-box;
}

.teammates-chat {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.04);
  border: 1px solid #e2dfdf;
  background-color: #ffffff;
  border-radius: 10px;
  display: none;
}


.teammates-chat.open {
  display: flex;
}

.teammates-chat.right {
  left: -340px;
}

.teammates-chat.top {
  left: 0;
  top: unset;
  bottom: 110px;
}

.teammates-chat__body {
  position: relative;
  width: 100%;
  height: 100%;
}

.teammates-chat__content {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.teammates-chat__head {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  margin-bottom: auto;
  box-sizing: border-box;
  background: #dfdfdf;
  -webkit-user-select: none;
  -webkit-app-region: drag;
}

.teammates-chat__type {
  display: flex;
  background-color: transparent;
  border: none;
  cursor: pointer;
  -webkit-app-region: no-drag;
}
.teammates-chat__type svg {
  width: 20px;
  height: 20px;
  fill: #00b9ff;
}

.teammates-chat__type.close svg {
  fill: #ff3286;
}

.teammates-chat__write {
  margin-top: auto;
}

.teammates-chat__messages {
      overflow: auto;
    height: calc(100% - 90px);
    top: 42px;
    position: absolute;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.teammates-chat__text {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
}

.teammates-chat__text span {
  min-width: max-content;
  max-width: 100%;
  border-radius: 20px;
  border: solid 1px #eeeeee;
  font-size: 14px;
  font-weight: 500;
  padding: 12px 24px;
}

.teammates-chat__text.right span {
  min-width: max-content;
  max-width: 100%;
  border-radius: 20px;
  background-color: #f8f8f8;
  color: #042141;
  font-size: 14px;
  font-weight: 500;
  padding: 12px 24px;
  margin-left: auto;
}

